import React, { useState, useEffect, useContext } from 'react'
import { UserContext } from '../../Store/UserContextProvider';

import LeftNav from './LeftNav'
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
export default function Adminusers() {
    var [user, setuser] = useState([])
    var { getAllUser, deleteUser } = useContext(UserContext)

    async function deleteRecord(_id) {
        if (window.confirm("Are You Sure to Delete this User")) {
            var item = {
                _id: _id
            }
            var response = await deleteUser(item)
            if (response.result === "Done")
                getApiData()
            else
                alert(response.message)
        }

    }
    async function getApiData() {
        var response = await getAllUser()
        if (response.result === "Done")
            setuser(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getApiData()
    }, [])
    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <LeftNav />
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-center text-light p-2 mt-1'>User List</h5>
                    <div className='table-responsive'>
                    <table className='table'>
                        <tbody>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>User Name</th>
                                <th>Email id</th>
                                <th>Phone</th>
                                <th>Role</th>
                                <th></th>
                            </tr>
                            {
                                user.map((item, index) => {
                                    return <tr key={index}>
                                        <td>{item._id}</td>
                                        <td>{item.name}</td>
                                        <td>{item.username}</td>
                                        <td>{item.email}</td>
                                        <td>{item.phone}</td>
                                        <td>{item.role}</td>
                                        <td><button className='btn text-primary' style={{ border: "none" }} onClick={() => deleteRecord(item._id)}><DeleteForeverOutlinedIcon /></button></td>
                                    </tr>
                                })
                            }


                        </tbody>

                    </table>
                    </div>

                </div>

            </div>
        </div>
    )
}
