import React, { useState, useEffect, useContext } from 'react'
import LeftNav from './LeftNav'
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
import AddIcon from '@mui/icons-material/Add';
import { Link } from 'react-router-dom';
import EditIcon from '@mui/icons-material/Edit';
import { ProductContext } from '../../Store/ProductContextProvider';


export default function AdminProduct() {
    var { getAllProduct } = useContext(ProductContext)
    var { deleteProduct } = useContext(ProductContext)
    var [product, setproduct] = useState([])

    async function deleteRecord(_id) {
        if (window.confirm("Are You Sure to Delete this Product")) {
           var item ={
            _id:_id
           }
            var response = await deleteProduct(item)
            if (response.result === "Done")
                getAPIData()
            else
                alert(response.message)
        }

    }
    
    async function getAPIData() {
        var response = await getAllProduct()

        if (response.result === "Done")
            setproduct(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getAPIData()
    }, [])
    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <LeftNav />
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-center text-light p-2 mt-1'>Product List<Link className='text-light' to='/admin-add-product'><AddIcon /></Link></h5>
                    <div className='table-responsive'>
                        <table className='table'>
                            <tbody>
                                <tr>
                                    <th>Id</th>
                                    <th>Name</th>
                                    <th>Maincategory</th>
                                    <th>Subcategory</th>
                                    <th>Brand</th>
                                    <th>Color</th>
                                    <th>Size</th>
                                    <th>Base Price</th>
                                    <th>Discount</th>
                                    <th>finalprice</th>
                                    <th>Stock</th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                                {
                                    product.map((item, index) => {
                                        return <tr key={index}>
                                            <td>{item._id}</td>
                                            <td>{item.name}</td>
                                            <td>{item.maincategory}</td>
                                            <td>{item.subcategory}</td>
                                            <td>{item.brand}</td>
                                            <td>{item.color}</td>
                                            <td>{item.size}</td>
                                            <td>&#8377;{item.baseprice}</td>
                                            <td>{item.discount}%</td>
                                            <td>&#8377;{item.finalprice}</td>
                                            <td>{item.stock}</td>
                                            <td><img src={`./public/images/${item.pic1}`}  width="50px" height="50px" className="rounded" /></td>
                                            <td><img src={`./public/images/${item.pic2}`}  width="50px" height="50px" className="rounded" /></td>
                                            <td><img src={`./public/images/${item.pic3}`}  width="50px" height="50px" className="rounded" /></td>
                                            <td><img src={`./public/images/${item.pic4}`}  width="50px" height="50px" className="rounded" /></td>
                                            <td><Link className='btn text-primary' style={{ border: "none" }} to={`/admin-update-product/${item._id}`} ><EditIcon /></Link></td>
                                            <td><button className='btn text-primary' style={{ border: "none" }} onClick={() => deleteRecord(item._id)}><DeleteForeverOutlinedIcon /></button></td>
                                        </tr>
                                    })
                                }
                            </tbody>

                        </table>
                    </div>


                </div>

            </div>
        </div>
    )
}
