import React, { useState ,useEffect, useContext} from 'react'
import LeftNav from './LeftNav'
import { useParams } from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
import { BrandContext } from '../../Store/BrandContextProvider'

export default function AdminUpdatebrand() {
    var [name, setname] = useState("")
    var {updateBrand} = useContext(BrandContext)
    var {getBrand} = useContext(BrandContext)
    var navigate = useNavigate()
    var {_id}= useParams()
    function getData(e) {
        setname(e.target.value)
    }
    async function postData(e) {
        e.preventDefault()
        var item = {
            name:name,
            _id:_id
            
        }
        var response = await  updateBrand(item)
        if (response.result === "Done")
            navigate("/admin-brand")
        else
            alert(response.message)
    }
    async function getApiData(){
        var item = {
            _id:_id
            
        }
        var response = await getBrand(item)
        if (response.result === "Done")
           setname(response.data.name)
        else
            alert(response.message)
    }

    useEffect(()=>{
        getApiData()
    },[])
    
    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <LeftNav />
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-center text-light p-2 mt-1'>Update Brand Page</h5>
                    <form onSubmit={postData}>
                        <div className="mb-3">
                            <label className="form-label">Name</label>
                            <input type="text" className="form-control" placeholder='Enter the brand' name='name' value={name} onChange={getData} />
                        </div>
                        <button type="submit" className='btn background text-light w-100'>Add</button>
                    </form>

                </div>

            </div>
        </div>
    )
}
