import React,{useEffect} from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
import AdminAddbrand from './Admin/AdminAddbrand'
import AdminAddmaincategory from './Admin/AdminAddmaincategory'
import AdminAddproduct from './Admin/AdminAddproduct'
import AdminAddsubcategory from './Admin/AdminAddsubcategory'
import Adminbrand from './Admin/Adminbrand'
import Admincheckoutpage from './Admin/Admincheckoutpage'
import Admincontactpage from './Admin/Admincontactpage'
import AdminHome from './Admin/AdminHome'
import AdminMaincategory from './Admin/AdminMaincategory'
import Adminnewslatter from './Admin/Adminnewslatter'
import Adminproduct from './Admin/Adminproduct'
import Adminsinglecheckout from './Admin/Adminsinglecheckout'
import Adminsinglecontact from './Admin/Adminsinglecontact'
import Adminsubcategory from './Admin/Adminsubcategory'
import AdminUpdatebrand from './Admin/AdminUpdatebrand'
import AdminUpdatemaincategory from './Admin/AdminUpdateMainctegory'
import AdminUpdateproduct from './Admin/AdminUpdateproduct'
import AdminUpdatesubcategory from './Admin/AdminUpdatesubctegory'
import AdminUsers from './Admin/AdminUsers'
import Cart from './Cart'
import Checkout from './Checkout'
import Confirmation from './Confirmation'
import ContactPage from './ContactPage'
import Footer from './Footer'
import ForgetOTP from './ForgetOTP'
import Forgetpassword from './ForgetPassword'
import ForgetUserName from './ForgetUserName'
import Home from './Home'
import Login from './Login'

import Navbar from './Navbar'
import Payment from './Payment'
import Profile from './Profile'
import Shop from './Shop'
import SignUP from './SignUP'
import SingleProductPage from './SingleProductPage'
import UpdateProfile from './UpdateProfile'

export default function App() {
  useEffect(() => {
    if (localStorage.getItem("role") && localStorage.getItem("role") !== "Admin") {
        if (window.location.pathname.includes("Admin") === true || window.location.pathname.includes("admin") === true) {
            window.location.href='/'
        }
    }
}, [])
  return (
    <>
      <BrowserRouter>
        <Navbar />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/shop/:mc/:sc/:br/:search" element={<Shop />} />
          <Route path="/login" element={<Login />} />
          <Route path="/SignUp" element={<SignUP />} />
          <Route path="/profile"  element={!(localStorage.getItem("login"))?<Login/>:<Profile />} />
          <Route path='/single-product-page/:_id' element={<SingleProductPage />} />
          <Route path='/cart'  element={!(localStorage.getItem("login"))?<Login/>:<Cart />} />
        <Route path='/update-profile'  element={!(localStorage.getItem("login"))?<Login/>:<UpdateProfile/>}/>
        <Route path='/checkout'  element={!(localStorage.getItem("login"))?<Login/>:<Checkout/>}/>
        <Route path='/contactpage' element={<ContactPage/>}></Route>
        <Route path='/confirm'  element={!(localStorage.getItem("login"))?<Login/>:<Confirmation/>}></Route> 
        <Route path='/forget-username' element={<ForgetUserName/>}></Route>
        <Route path='/forget-otp' element={<ForgetOTP/>}></Route>
        <Route path='/forget-password' element={<Forgetpassword/>}></Route>
        <Route path='/payment/:_id'  element={!(localStorage.getItem("login"))?<Login/>:<Payment/>}></Route>
        
        
        <Route path='/admin-home'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>:<AdminHome/>}></Route>
        <Route path='/admin-users'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminUsers/>}></Route>
        <Route path='/admin-maincategory'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminMaincategory/>}></Route>
        <Route path='/admin-add-maincategory'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminAddmaincategory/>}></Route>
        <Route path='/admin-update-maincategory/:_id'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminUpdatemaincategory/>}></Route>
        <Route path='/admin-subcategory'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Adminsubcategory/>}></Route>
        <Route path='/admin-add-subcategory'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminAddsubcategory/>}></Route>
        <Route path='/admin-update-subcategory/:_id'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminUpdatesubcategory/>}></Route>
        <Route path='/admin-brand'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Adminbrand/>}></Route>
        <Route path='/admin-add-brand'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminAddbrand/>}></Route>
        <Route path='/admin-update-brand/:_id'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminUpdatebrand/>}></Route>
        <Route path='/admin-product'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Adminproduct/>}></Route>
        <Route path='/admin-add-product'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminAddproduct/>}></Route>
        <Route path='/admin-update-product/:_id'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <AdminUpdateproduct/>}></Route>
        <Route path='//admin-newslatter'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Adminnewslatter/>}></Route>
        <Route path='//admin-contact'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Admincontactpage/>}></Route>
        <Route path='//admin-single-contact/:_id'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Adminsinglecontact/>}></Route>
        <Route path='//admin-single-checkout/:_id'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Adminsinglecheckout/>}></Route>
        <Route path='//admin-checkout/'  element={!(localStorage.getItem("login"))?<Login/>:localStorage.getItem("role")==="User"?<Profile/>: <Admincheckoutpage/>}></Route>
        </Routes>
      </BrowserRouter>
      <Footer />
    </>
  )
}
