import React, { useState, useEffect, useContext } from 'react'
import LeftNav from './LeftNav'
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { Link } from 'react-router-dom';
import  EditIcon from '@mui/icons-material/Edit';
import  { ContactContext } from '../../Store/ContactContextProvider';

export default function Admincontactpage() {
    var [contact, setcontact] = useState([])
    var {getAllContact}= useContext(ContactContext)
    var {deleteContact}= useContext(ContactContext)

    
    async function deleteRecord(_id) {
        if(window.confirm("Are You Sure to Delete this Brand")){
            var item ={
                _id:_id
            }
            var response = await deleteContact(item)
            if (response.result === "Done")
               getApiData()
            else
                alert(response.message)
        }
        
    }
    async function getApiData() {

       var response = await getAllContact()
        if (response.result === "Done")
            setcontact(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getApiData()
    }, [])
    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <LeftNav />
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-center text-light p-2 mt-1'>Contact Us List</h5>
                    <div className='table-responsive'>
                    <table className='table'>
                        <tbody>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Email Id</th>
                                <th>Phone</th>
                                <th>Status</th>
                                <th></th>
                            </tr>
                            {
                                contact.map((item, index) => {
                                    return <tr key={index}>
                                        <td>{item._id}</td>
                                        <td>{item.name}</td>
                                        <td>{item.email}</td>
                                        <td>{item.phone}</td>
                                        <td>{item.status}</td>
                                        <td><Link className='btn text-primary' style={{ border: "none" }} to={`/admin-single-contact/${item._id}`} ><VisibilityIcon/></Link></td>
                                        <td>{item.status!=="Active"?<button className='btn text-primary' style={{ border: "none" }} onClick={() => deleteRecord(item._id)}><DeleteForeverOutlinedIcon /></button>:""}</td>
                                    </tr>
                                })
                            }



                        </tbody>

                    </table>

                    </div>

                </div>

            </div>
        </div>
    )
}
