import React, { useState } from 'react'
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import { Link } from '@mui/material';
import { useNavigate } from 'react-router-dom';
export default function Forgetpassword() {
    
    
    let [user, setuser] = useState({
        password: "",
        cpassword: ""
    })
    var navigate = useNavigate()
    function getData(name, value) {
        setuser((oldData) => {
            return {
                ...oldData,
                [name]: value
            }
        })
    }
    async function postData() {
    if(user.password===user.cpassword){
        var item = {
            username : localStorage.getItem("resetuser"),
            password : user.password
           }
           var response = await fetch("/resetpassword-password",{
            method:"post",
            headers:{
                "content-type":"application/json"
            },
            body:JSON.stringify(item)
           })
           response = await response.json()
           if(response.result === "Done"){
            alert(response.message)
            navigate("/login")
           }
           else
           alert(response.message)
        }
        else
        alert("Password And Confirm Password Does Not Matched!!!!")
    }
    
    return (
        <Grid container spacing={2}>
            <Grid item md={2} xs={12}>
            </Grid>
            <Grid item md={8} xs={12}>
                <h5 className='background text-light text-center p-2 mt-1'>Login Section</h5>
                <form>
                    <Box
                        component="form"
                        sx={{
                            '& > :not(style)': { m: 1, width: '99%' },
                        }}
                        noValidate
                        autoComplete="off"
                    >
                        <TextField
                            onChange={(e) => {
                                getData(e.target.name, e.target.value)
                            }}
                            id="outlined-password-input"
                            label="Password"
                            type="Password"
                            placeholder='Enter Your New Password'
                            name='password'
                        />
                        <TextField
                            onChange={(e) => {
                                getData(e.target.name, e.target.value)
                            }}
                            id="outlined-password-input"
                            label="Comfirm-Password"
                            type="Password"
                            placeholder='Confirm Password'
                            name='cpassword'
                        />
                        <Button variant="contained" className='background' onClick={postData}>Reset</Button>
                        
                    </Box>

                </form>
            </Grid>
            <Grid item md={2} xs={12}>
            </Grid>
        </Grid>
    )
}


