import React from 'react'
import { Link } from 'react-router-dom'

export default function LeftNav() {
    return (
        <>
            <div className="list-group">
                <h5 className='background text-light text-center p-2 mt-1'> Menu</h5>
                <Link to="/admin-home" className="list-group-item list-group-item-action background text-light ">Home</Link>
                <Link to="/admin-users" className="list-group-item list-group-item-action background text-light ">User list</Link>
                <Link to="/admin-maincategory" className="list-group-item list-group-item-action background text-light ">Maincategory</Link>
                <Link to="/admin-subcategory" className="list-group-item list-group-item-action background text-light ">Subcategory</Link>
                <Link to="/admin-brand" className="list-group-item list-group-item-action background text-light ">Brands</Link>
                <Link to="/admin-product" className="list-group-item list-group-item-action background text-light ">Products</Link>
                <Link to="/admin-checkout" className="list-group-item list-group-item-action background text-light ">Checkout</Link>
                <Link to="/admin-contact" className="list-group-item list-group-item-action background text-light ">Contact</Link>
                <Link to="/admin-newslatter" className="list-group-item list-group-item-action background text-light ">Newslatter</Link>
                
             </div>
        </>
    )
}
