import React, { useState, useEffect, useContext } from 'react'
import LeftNav from './LeftNav'
import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';
import AddIcon from '@mui/icons-material/Add';
import { Link } from 'react-router-dom';
import  EditIcon from '@mui/icons-material/Edit';
import  { BrandContext } from '../../Store/BrandContextProvider';
export default function Adminbrand() {
    var [brand, setbrand] = useState([])
    var {getAllBrand}= useContext(BrandContext)
    var {deleteBrand}= useContext(BrandContext)

    
    async function deleteRecord(_id) {
        if(window.confirm("Are You Sure to Delete this Brand")){
            var item ={
                _id:_id
            }
            var response = await deleteBrand(item)
            if (response.result === "Done")
               getApiData()
            else
                alert(response.message)
        }
        
    }
    async function getApiData() {

       var response = await getAllBrand()
        if (response.result === "Done")
            setbrand(response.data)
        else
            alert(response.message)
    }
    useEffect(() => {
        getApiData()
    }, [])
    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <LeftNav />
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-center text-light p-2 mt-1'>Brand List<Link  className='text-light' to='/admin-add-brand'><AddIcon/></Link></h5>
                    <table className='table'>
                        <tbody>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th></th>
                                <th></th>
                            </tr>
                            {
                                brand.map((item, index) => {
                                    return <tr key={index}>
                                        <td>{item._id}</td>
                                        <td>{item.name}</td>
                                        <td><Link className='btn text-primary' style={{ border: "none" }} to={`/admin-update-brand/${item._id}`} ><EditIcon /></Link></td>
                                        <td><button className='btn text-primary' style={{ border: "none" }} onClick={() => deleteRecord(item._id)}><DeleteForeverOutlinedIcon /></button></td>
                                    </tr>
                                })
                            }
                            
                           
                        </tbody>

                    </table>

                </div>

            </div>
        </div>
    )
}
