import React, { useContext, useState, useEffect } from 'react'
import LeftNav from './LeftNav'
import { UserContext } from '../../Store/UserContextProvider'
import pic from '../../assets/images/user.jpg'
export default function AdminHome() {
    var { getUser } = useContext(UserContext)
    var [user, setuser] = useState({})
    async function getAPIData() {
        

         var response = await getUser()
        setuser(response.data)

    }
    useEffect(() => {
        getAPIData()
    }, [])
    return (
        <div className='container-fluid'>
            <div className='row'>
                <div className='col-xl-2 col-lg-3 col-md-4 col-sm-6 col-12'>
                    <LeftNav />
                </div>
                <div className='col-xl-10 col-lg-9 col-md-8 col-sm-6 col-12'>
                    <h5 className='background text-center text-light p-2 mt-1'>Admin Home Page</h5>
                    <div className='row' >
                        <div className='col-md-6 col-12'>
                            <img src={user.pic?`/public/images/${user.pic}`:pic} width='100%' height='500px' className='rounded'></img>
                        </div>
                        <div className='col-md-6 col-12'>
                           <table className='table'>
                            <tbody>
                            <tr>
                                <th>Name</th>
                                <td>{user.name}</td>
                            </tr>
                            <tr>
                                <th>User Name</th>
                                <td>{user.username}</td>
                            </tr>
                            <tr>
                                <th>Email Address</th>
                                <td>{user.email}</td>
                            </tr>
                            <tr>
                                <th>Phone Number</th>
                                <td>{user.phone}</td>
                            </tr>
                            <tr>
                                <th>House Or Building Number</th>
                                <td>{user.addressline1}</td>
                            </tr>
                            <tr>
                                <th>Street Number or Near By</th>
                                <td>{user.addressline2}</td>
                            </tr>
                            <tr>
                                <th>Locality</th>
                                <td>{user.addressline3}</td>
                            </tr>
                            <tr>
                                <th>PIN CODE</th>
                                <td>{user.pin}</td>
                            </tr>
                            <tr>
                                <th>City</th>
                                <td>{user.city}</td>
                            </tr>
                            <tr>
                                <th>State</th>
                                <td>{user.state}</td>
                            </tr>
                            </tbody>
                           </table>
                           </div>

                    </div>
                </div>

            </div>
        </div>
    )
}
